@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@500;600;700&family=Rubik:wght@300;400;500&display=swap);
:root {
  font-size: 14px;
}
html[data-theme='light'] {
  --white: #fff;
  --lightgrey: #f2f2f2;
  --grey: #808080;
  --black: #2f2f2f;
  --orange: #ee974e;
  --aquagreen: #50ba9e;
}


html[data-theme='dark'] {
  --white: #181a1b;
  --lightgrey: #1f2223;
  --grey: #808080;
  --black: #f3f3f3;
  --orange: #ee974e;
  --aquagreen: #50ba9e;
}

#root {
  background-color: var(--lightgrey);
}

body {
  font-family: 'Rubik', sans-serif;
  background-color: var(--lightgrey);
  color: var(--black);
}

a {
  color: var(--black);
  text-decoration: none;
}

a:hover {
  color: var(--black);
}

ul {
  text-decoration: none;
  list-style-type: none;
}

.main {
  min-height: 87.5vh;
}

.container {
  max-width: 1200px;
  padding: 0 40px;
  width: 100%;
  margin: 0 auto;
}

.section {
  padding: 60px 0;
}

.title_section {
  font-size: 46px;
  font-family: 'Nunito', sans-serif;
  font-weight: 800;
  text-align: center;
  color: var(--aquagreen);
  margin-bottom: 30px;
}

.input {
  height: 4.8rem;
  width: 32rem;
  padding-left: 1rem;
  margin-right: 3rem;
  border-radius: 5px;
  border: none;
  color: #72848d;
  background: pink;
}

.button {
  min-width: 115px;
  padding: 10px 30px;
  border-radius: 20px;
  font-size: 14px;
  font-weight: 400;
  cursor: pointer;
  letter-spacing: .9px;
  text-transform: uppercase;
  text-decoration: none;
  border: none;
  color: #ffffff;
  background-color: var(--grey);
  transition: all .3s;
}

.button:hover {
  opacity: .85;
}

.card-image {
  max-width: 150px;
  width: 100%;
  margin: 0 auto;
}

.card {
  width: 250px;
  min-height: 350px;
  padding: 10px;
  margin: 10px 5px;
  border-radius: 9px;
  cursor: pointer;
  background-color: var(--white);
  transition: all .3s;
}

.card:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.card-contain {
  display: flex;
  justify-content: space-between;
  padding: 20px 10px 0;
}

.card-contain__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.card-contain__text_title {
  font-size: 24px;
  font-weight: 500;
  text-transform: capitalize;
  margin-bottom: 10px;
}

.card-contain__description {
  margin: 0;
}

/* PopUp Menu */

.pop-up-menu {
  position: fixed;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  width: 250px;
  z-index: 20;
  background-color: var(--aquagreen);
}

.pop-up-menu__content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 4rem;
  width: 100%;
}

.pop-up-menu__item {
  padding: 10px 40px;
}

.transition {
  -webkit-transform: translate(20rem);
          transform: translate(20rem);
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

.transition-back {
  -webkit-transform: translate(-20rem);
          transform: translate(-20rem);
  transition: -webkit-transform 1s ease-in-out;
  transition: transform 1s ease-in-out;
  transition: transform 1s ease-in-out, -webkit-transform 1s ease-in-out;
}

.menu-pop-up-menu__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 2rem 2rem 1rem 1rem;
  cursor: pointer;
}

.menu__item {
  display: flex;
  align-items: self-start;
  justify-content: center;
}
.menu__item:hover {
  scale: 1.2;
}

.menu__item__img {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.menu__name {
  font-size: 16px;
  font-weight: 200;
  text-decoration: none;
  opacity: .8;
  transition: all .3s;
}

.menu__name:hover {
  opacity: 1;
}

.selected {
  border-bottom: 1px solid var(--lightgrey);
}

/* .selected::before {
  content: "♥ ";
} */

.pop-up-menu__close-icon {
  position: absolute;
  top: 30px;
  right: 20px;
  cursor: pointer;
}

.pop-up-menu__close-icon svg {
  pointer-events: none;
}

.pop-up-menu__close-icon:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

@media (max-width: 768px) {
  .title_section {
    font-size: 34px;
  }
}
.intro__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.intro__description {
  max-width: 550px;
}

.intro__title {
  font-size: 64px;
  font-family: 'Nunito', sans-serif;
  font-weight: 800;
  color: #50ba9e;
  margin-bottom: 30px;
}

.intro__text {
  font-size: 24px;
}

.intro__image {
  max-width: 400px;
  width: 100%;
}

@media (max-width: 768px) {
  .intro__image {
    max-width: 380px;
    width: 50%;
  }
  .intro__title {
    font-size: 46px;
  }
  .intro__text {
    font-size: 18px;
  }
}
.about-us {
  padding: 60px 0;
}

.about-us__content {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.about-us__card {
  max-width: 300px;
  width: 100%;
  padding: 15px 10px;
}

.about-us__card-title {
  display: flex;
  margin-bottom: 10px;
}

.about-us__card-img {
  width: 90px;
  height: 90px;
  margin-right: 20px;
  /* border-radius: 50%; */
}

.about-us__card-name {
  display: flex;
  align-items: center;
  margin: 7px 0 5px;
  font-size: 24px;
  font-weight: 500;
  transition: all .3s;
}

.about-us__card-name:hover {
  color: var(--orange);
}

.about-us__card-icon {
  font-size: 16px;
  margin-right: 5px;
}

.about-us__card-role {
  margin: 7px 0 5px;
  font-size: 16px;
  font-weight: 500;
}

.about-us__card-text {
  font-size: 14px;
}


.advantages__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.header {
  box-shadow: 0 0 20px 0 rgb(0 0 0 / 10%);
  background-color: var(--lightgrey);
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}

.header__button {
  color: #fff;
  background-color: var(--orange);
}

.hamburger__line,
.hamburger:after,
.hamburger:before {
  content: "";
  position: absolute;
  display: inline-block;
  height: 3px;
  width: 100%;
  background-color: var(--grey);
  border-radius: 5px;
}

.hamburger {
  display: inline-block;
  position: relative;
  width: 20px;
  height: 15px;
  cursor: pointer;
}

.hamburger:before {
  top: 0;
  left: 0;
}

.hamburger:after {
  bottom: 0;
  left: 0;
}

.hamburger__line {
  top: calc( 50% - 1.5px);
  left: 0;
}

.title-link {
  font-size: 40px;
  color: var(--black);
  max-width: 310px;
  width: 100%;
  transition: all .3s;
}

.title-link:hover {
  color: var(--orange);
  scale: 1.2;
}

.button_theme {
  background-color: transparent;
  border: transparent;
  color: var(--black);
}

.theme-switcher__line {
  color: var(--black);
}

.theme-switcher__icon_light {
  font-size: 16px;
}

@media (max-width: 768px) {
  .title-link {
    max-width: 180px;
  }
  .header__button {
    min-width: 80px;
    padding: 10px 10px;
  }
}
.footer {
  width: 100%;
  background-color: var(--lightgrey);
  box-shadow: 0 10px 20px 10px rgb(0 0 0 / 10%);
}

.footer__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
}

.github {
  cursor: pointer;
  font-size: 14px;
  background-image: url('/public/icons/github.svg');
  background-size: 35px;
  background-repeat: no-repeat;
  background-position: left center;
  color: var(--grey);
  font-family: 'Open Sans', sans-serif;
  transition: all 0.3s;
}
.github:hover {
  color: var(--orange);
}

.rss {
  position: relative;
  width: 140px;;
  height: 32px;
  background-image: url('/icons/rss.svg');
  background-size: contain;
  background-repeat: no-repeat;
}

.rss-year {
  position: absolute;
  bottom: 0;
  right: 0;
  font-size: 21px;
  letter-spacing: -2px;
  color: var(--grey);
  line-height: 0.8;
  font-weight: 900;
  transition: 0.3s;
}
.rss:hover .rss-year {
  right: -5px;
  letter-spacing: 0;
}

.cover {
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(41, 41, 41, 0.6);
  z-index: 10;
  width: 100vw;
  height: 100vh;
  cursor: pointer;
}


.dictionary-unit {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  width: 150px;
  height: 40vh;
  margin-bottom: 20px;
  background: var(--white);
  border-radius: 9px;
  transition: all .3s;
}

.dictionary-unit:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.dictionary-unit__image {
  max-width: 150px;
  margin: 0;
  border-radius: 9px 9px 0 0;
}

.dictionary-unit__contain {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
}

.dictionary-unit__text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: lightcoral;
  font-size: 20px;
}

.dictionary {
  padding: 60px 0;
}

.dictionary__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.dictionary__container_cards {
  flex-direction: column;
}

.dictionary-game {
  width: 100%;
  border-radius: 10px;
  background-color: #fcd5b4b8;
  margin-bottom: 40px;
}

.dictionary-game__container {
  display: flex;
  flex-direction: column;
  padding: 20px 0;
}

.dictionary-game__description {
  font-size: 24px;
  text-align: center;
}

.dictionary-game__buttons {
  display: flex;
  justify-content: space-evenly;
  margin: 5px;
}

.button_dictionary {
  background-color: var(--orange);
}
.pagination {
  display: flex;
  justify-content: center;
  margin: 20px 0;
}
.pagination__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  align-items: center;
  margin: 0;
  padding: 0;
}

.button_pagination {
  height: 32px;
  min-width: 32px;
  margin: 0 3px;
  padding: 0 6px;
  font-size: 14px;
  box-sizing: border-box;
  text-align: center;
  font-weight: 400;
  border-radius: 50%;
  border: none;
  background-color: transparent;
  color: var(--black);
  fill: var(--black);
  transition: all .3s;
}

.button_pagination .selected {
  color: #ffffff;
  background-color: #FF7171;
}

.button_pagination:hover {
  background-color: rgba(0, 0, 0, 0.04);
}

.pagination__icon {
  width: 24px;
  height: auto;
  fill: var(--color-black);
}
.dictionary-card {
  padding: 5px 20px;
  display: flex;
  border-bottom: 1px solid lightgray;
  border-top: 1px solid lightgray;
}

.dictionary-card__image {
  object-fit: scale-down;
  width: 30%;
  padding: 5px;
}

.dictionary-card__contain {
  font-size: 20px;
  margin-left: 30px;
}

.item_thin {
  font-size: 16px;
  font-weight: 200;
  padding-bottom: 10px;
}

.main-item_thin {
  font-size: 20px;
  font-weight: 400;
}

.dictionary-card__main-text {
  font-size: 24px;
  padding-bottom: 15px;
}

.dictionary-card__text {
  font-weight: 300;
  padding-bottom: 10px;
}

.audio-button {
  cursor: pointer;
}





.statistic-words {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 40px 0 70px;
}

.statistic-words__wrapper {
  display: flex;
