}

.statistic-words__number {
  font-weight: 600;
  font-size: 76px;
  line-height: 1em;
}

.statistic-words__description {
  font-size: 24px;
}

.statistic-games__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.statistic-games__card {
  padding: 20px;
  margin: 0 40px 20px;
  max-width: 400px;
  width: 100%;
  border-radius: 10px;
  background-color: #fcd5b4b8;
}

.statistic-games__title {
  margin-bottom: 15px;
