}

.statistic-games__icon {
  padding-right: 10px;
}
.statistic-image {
  margin-top: 30px;
  text-align: center;
}

.statistic-image__image {
  max-width: 400px;
  width: 100%;
}
.games {
  display: flex;
  flex-wrap: wrap;
  padding: 60px 0;
  justify-content: center;
}

.games-card {
  margin: 0 20px;
}

.games-card .card {
  min-height: 200px;
}

.game-settings {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 100;
}

.game-settings__button {
  opacity: 0.5;
  top: 0;
  right: 0;
  transition: all .3s;
}

.game-settings__button:hover {
  opacity: 1;
}

.game-settings__icon {
  width: 24px;
  height: 24px;
  fill: var(--black);
