/* Audiocall */
.audiocall {
  width: 100%;
  /* height: 100vh; */
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.audiocall__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.voice__button {
  border-radius: 50%;
  background-color: transparent;
  border: none;
  transition: all .3s;
}

.voice__button:hover {
  background-color: rgba(0, 0, 0, 0.1);
  /* background-color: rgba(255, 255, 255, .2); */
}

.voice__icon {
  width: 90px;
  border-radius: 50%;
  border: 3px solid white;
  padding: 5px;
  margin: 20px;
  fill: #fff;
}

.audiocall__button {
  margin: 10px 5px;
  padding: 10px 30px;
  border: none;
  font-size: 14px;
  border-radius: 20px;
  text-transform: uppercase;
  color: #222;
}

.audiocall__button_answer {
  background-color: transparent;
}

.audiocall__button_answer:hover {
  background-color: rgba(0, 0, 0, 0.1);
  /* background-color: rgba(255, 255, 255, .2); */
}
.audiocall-popup {
  position: fixed;
  top: 20%;
  left: calc(50% - 230px);
  width: 450px;
  height: 350px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 10px;
  background-color: var(--white);
  border-radius: 8px;
  z-index: 100;
}

.audiocall-popup-image__image {
  max-width: 200px;
  width: 100%;
  height: auto;
}

.audiocall__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
}

.audiocall__answer {
  font-size: 16px;
}

.audiocall-popup__buttons {
  display: flex;
  justify-content: space-around;
}

.button_audiocall {
  margin: 0 5px;
}
/* Audiocall */

.audiocall {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.audiocall__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.voice__button {
  border-radius: 50%;
  background-color: transparent;
  border: none;
  transition: all .3s;
}

.voice__button:hover {
  background-color: rgba(0, 0, 0, 0.1);
  /* background-color: rgba(255, 255, 255, .2); */
}

.voice__icon {
  width: 90px;
  border-radius: 50%;
  border: 3px solid var(--black);
  padding: 5px;
  margin: 20px;
  fill: var(--black);
}

.audiocall__button {
  margin: 10px 5px;
  padding: 10px 30px;
  border: none;
  font-size: 14px;
  border-radius: 20px;
  text-transform: uppercase;
  color: var(--black);
}

.audiocall__button:hover {
  background-color: #808080;
}

.audiocall__button_answer {
  background-color: transparent;
}

.audiocall__button_answer:hover {
  background-color: rgba(0, 0, 0, 0.1);
  /* background-color: rgba(255, 255, 255, .2); */
}

.audiocall__words, .audiocall__help {
  padding-top: 60px;
}

.results-popup-image {
  margin-top: -100px;
}

.results-popup-image__image {
  max-width: 200px;
  width: 100%;
  height: auto;
}
.autorization {
  display: flex;
  padding: 60px 0;
  justify-content: center;
}
.registration {
  display: flex;
  padding: 60px 0;
  justify-content: center;
}

